.product-item{display:flex;align-items:center;justify-content:space-between;padding:4px 16px;margin:8px 0;position:relative}.product-item:hover{background-color:var(--background-color)}.product-item:hover>.remove-btn{display:block}.product-item::after{content:"";position:absolute;left:50%;transform:translateX(-50%);bottom:-6px;width:80%;height:1px;background-color:var(--text-color-opacity);opacity:20%}.image-item{width:50px;height:50px;border-radius:14px;border:2px solid var(--box-color);box-shadow:2px 2px 0 var(--box-color);margin-right:8px;background-color:#fff}.info{flex:1;margin:0 4px}.name{display:block;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;overflow:hidden;text-overflow:ellipsis;font-weight:800}.size{border:1px solid var(--box-color);display:inline-block;text-align:center;width:18px;height:18px;border-radius:4px;box-shadow:1px 1px 0 var(--box-color);font-size:14px;margin-left:30px;position:relative;top:-3px;cursor:no-drop}.quantily{display:flex;font-size:14 px;font-weight:800}.quantily-btn{background-color:var(--yellow-color);opacity:60%;border:none;width:16px;height:16px;font-size:10px;padding:2px;margin:0 4px;cursor:pointer;color:#fff;border-radius:4px}.quantily-btn:hover{opacity:1}.input-quantity{width:20px;text-align:center}.price{text-align:left;font-size:12px;font-weight:800}.unit{font-size:12px;position:relative}.unit::after{content:"đ";position:absolute;right:-10px}.remove-btn{display:none;position:absolute;right:10px;width:20px;height:20px;margin-right:16px;line-height:20px;text-align:center;border-radius:50%;background-color:var(--red-color);opacity:50%;font-size:15px;color:#fff;border:none;cursor:pointer}.remove-btn:hover{opacity:1}